/* 다크 배경에 맞춘 헤더 */
.stechHeader {
  padding: 3.75rem 3.75rem 0 3.75rem;
  background: #141414;
  color: #e5e7eb;
}

.headerContainer {
  height: 7.194rem;
  display: flex;
  align-items: center;
  gap: 2.5rem;
  padding-bottom: 3.75rem;
  border-bottom: 1px solid #fff;
}

.header-team-box {
  height: 3.444rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex: 0 0 auto;
}
.header-team-name {
  color: #fff;
  font-weight: 600;
  font-size: 2rem;
  line-height: 1.1;
  white-space: nowrap;
}
.header-team-logo-box {
  height: 3.444rem;
  width: 3.444rem;
  flex: 0 0 3.444rem;
}
.header-team-logo-img.svg-logo {
  width: 3.444rem;
  height: 3.44rem;
  object-fit: cover;
  object-position: center;
}
.header-team-logo-img.png-logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

.bottomRow {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

/* ───────── 필터 버튼 그룹 ───────── */
.filterGroup {
  display: flex;
  gap: 2.5rem;
}

.filterButton,
.resetButton {
  width: 8.125rem;
  height: 3.444rem;
  font-size: 1.125rem;
  border-radius: 0.5rem;
  border: 1.287px solid var(--Gray-100, #e4e7e9);
  background: rgba(110, 110, 110, 0.2);
  color: #c2c2c2;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  cursor: pointer;
}
.filterButton.active,
.filterButton:hover {
  background: rgba(255, 255, 255, 0.14);
  color: #fff;
  border-color: #6e6e6e;
}

.resetButton {
  width: 6.25rem;
  border-color: #1a58e0;
  background: #1a58e0;
}

/* ───────── New Video 버튼 ───────── */
.newVideoButton {
  border-radius: 1.25rem;
  background: #1a58e0;
  width: 8.438rem;
  height: 3.444rem;
  font-size: 1.125rem;
  font-weight: 700;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: transform 0.15s, box-shadow 0.15s;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.35);
}
.newVideoButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(59, 130, 246, 0.45);
}
.newVideoButton:active {
  transform: translateY(0);
}

/* Date picker 래퍼 */
.datePickerWrap {
  position: relative;
}

/* TYPE 드롭다운 */
.typePickerWrap {
  position: relative;
}
.typeDropdown {
  position: absolute;
  top: calc(100% + 4px);
  left: 0;
  background: #1c1d21;
  border: 1px solid #303136;
  border-radius: 6px;
  padding: 4px 0;
  width: 180px;
  z-index: 120;
  list-style: none;
}
.typeItem {
  width: 100%;
  padding: 6px 12px;
  background: transparent;
  border: none;
  color: #e5e7eb;
  cursor: pointer;
}
.typeItem:hover {
  background: #2a2b30;
}

/* ───── OPPS 메가 드롭다운 ───── */
.oppsPickerWrap {
  position: relative;
}

.oppsMega {
  position: absolute;
  top: 100%;
  left: 0;
  display: flex;
  background: #1f1f23;
  border: 1px solid #2b2b31;
  border-radius: 10px;
  min-width: 520px;
  overflow: hidden;
  z-index: 120;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.35);
}
.oppsLeagues,
.oppsTeams {
  list-style: none;
  margin: 0;
  padding: 6px 0;
  max-height: 280px;
  overflow: auto;
}
.oppsLeagues {
  width: 170px;
  border-right: 1px solid #2b2b31;
}
.leagueItem {
  width: 100%;
  text-align: left;
  padding: 10px 14px;
  background: transparent;
  border: 0;
  color: #eaeaea;
  cursor: pointer;
  font-size: 14px;
}
/* 🔧 버그 수정: 비어있는 셀렉터 제거 */
.leagueItem:hover,
.leagueItem.active {
  background: rgba(255, 255, 255, 0.06);
}

.oppsTeams {
  width: 350px;
}
.oppsItem {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 14px;
  background: transparent;
  border: 0;
  color: #fff;
  text-align: left;
  cursor: pointer;
  font-size: 14px;
}
.oppsItem:hover {
  background: rgba(255, 255, 255, 0.06);
}

.opps-team-logo-img-box {
  width: 1.25rem;
  height: 1.25rem;
  flex: 0 0 1.25rem; /* 통일 */
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.opps-team-logo-img.svg-logo,
.opps-team-logo-img.png-logo {
  width: 20px;
  height: 20px;
  object-fit: contain;
  object-position: center;
}

.oppsEmpty {
  padding: 12px 14px;
  opacity: 0.7;
  color: #cfcfcf;
}

/* ====== 게임 리스트 ====== */
.game-container {
  display: flex;
  flex-direction: column;
  padding-left: 3.75rem;
  padding-right: 3.75rem;
  margin-top: 1.5rem;
}

.game-header {
  width: 100%;
  height: 5rem;
  display: grid;
  grid-template-columns: 10rem minmax(18rem, 1.6fr) minmax(12rem, 1fr) 8rem 6rem;
  color: white;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.12);
}
.game-header-cell {
  justify-self: center;
}

.game-list {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.game-card {
  width: 100%;
  height: 3.75rem;
  border-radius: 0.5rem;
  border: 0.236px solid #6e6e6e;
  background: rgba(110, 110, 110, 0.2);
  color: white;
  display: grid;
  grid-template-columns: 10rem minmax(18rem, 1.6fr) minmax(12rem, 1fr) 8rem 6rem;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.08s, box-shadow 0.12s;
}
.game-card:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.24);
}
.game-card > div {
  justify-self: center;
}

.game-results {
  display: grid;
  grid-template-columns: minmax(0, 1fr) auto minmax(0, 1fr);
  align-items: center;
  column-gap: 1.25rem;
}

.game-team.left,
.game-team.right {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.game-team-logo {
  width: 1.563rem; /* 25px */
  height: 1.563rem;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.game-team-logo-img.svg-logo {
  width: 4rem;
  height: 4rem;
  object-fit: cover;
  object-position: center;
}
.game-team-logo-img.png-logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

.game-score {
  justify-self: center;
  width: 3.75rem;
  height: 1.375rem;
  flex-shrink: 0;
  background: #363636;
  font-size: 0.875rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}

.reportY {
  color: #1a58e0;
}
.reportN {
  color: #ff2b6e;
}

/* 반응형 컬럼 축소 */
@media (max-width: 1080px) {
  .game-header,
  .game-card {
    grid-template-columns: 8rem 1.4fr 1fr 6.5rem 5.5rem;
  }
}
@media (max-width: 760px) {
  .filterGroup {
    display: none;
  }
}
