.statTeamContainer {
  color: #ffffff;
  width: 100%;
  padding-left: 3.75rem;
  padding-right: 3.75rem;
}

.tournament-header {
  display: flex;
  align-items: center;
  padding-top: 4.063rem;
}
.dropdown-group {
  display: flex;
  gap: 1.875rem;
}

.dropdown-container {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #c2c2c2;
  font-size: 0.875rem;
  font-weight: 700;
  cursor: pointer;
}

.dropdown-trigger {
  width: 4.375rem;
  height: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0.75rem;
  border-radius: 0.5rem;
  border: 1px solid var(--Gray-100, #e4e7e9);
  background: rgba(110, 110, 110, 0.2);
}

.dropdown-arrow {
  width: 1rem;
  height: 1rem;
  color: #c2c2c2;
}

.groups-container {
  display: flex;
  justify-content: space-between;
  gap: 1.25rem;
  padding-top: 3.125rem;
}
.group-section {
  width: 45.625rem;
}
.no-group-img {
  width: 100%;
}

.group-header,
.playoffs-header,
.final-header,
.promotion-header {
  display: flex;
  align-items: center;
  font-size: 1.25rem;
  border-bottom: 1px solid #ffffff;
  padding-bottom: 1rem;
  color: #f5f5f5;
  font-weight: 700;
}

.group-standings {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

/* 헤더 행 */
.standings-header {
  display: grid;
  grid-template-columns: 3.75rem 3.75rem 1fr 3.75rem 3.75rem 3.75rem 5rem 3.75rem 3.75rem;
  font-weight: bold;
  color: #a5a5a5;
  padding-top: 0.5rem;
}

/* 데이터 행 */
.standings-row {
  display: grid;
  grid-template-columns: 3.75rem 3.75rem 1fr 3.75rem 3.75rem 3.75rem 5rem 3.75rem 3.75rem;
  border-radius: 0.5rem;
  background-color: rgba(255, 255, 255, 0.4);
  transition: transform-color 0.2s;
}

.standings-row.rank-1st {
  border: 3px solid #d5a11e;
}

.standings-row.rank-2nd {
  border: 3px solid #fff;
}

.standings-row.rank-3rd {
  border: 3px solid #cd7f32;
}

/* 각 셀 공통 스타일 */
.standings-cell {
  padding: 0.75rem 0.5rem;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #a5a5a5;
}

/* 순위 셀 */
.rank-cell {
  font-weight: bold;
  font-size: 18px;
  color: #ffffff;
}

/* 로고 셀 */
.logo-cell {
  padding: 0.5rem;
}

.team-logo {
  width: 1.563rem; /* 25px */
  height: 1.563rem;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.team-logo-img.svg-logo {
  width: 4rem;
  height: 4rem;
  object-fit: cover;
  object-position: center;
}
.team-logo-img.png-logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}
/* 팀명 셀 */
.team-cell {
  text-align: left;
  justify-content: flex-start;
  font-weight: 500;
  padding-left: 0.75rem;
  color: #ffffff;
}
.team-cell.title {
  color: #a5a5a5;
}

/* 통계 셀 */
.stat-cell {
  font-weight: 500;
  color: #ffffff;
}

.match-row,
.match-header {
  display: grid;
  grid-template-columns: 12.5rem 2fr 1.5fr 10rem;
}

.match-header div,
.match-row div {
  text-align: center;
  vertical-align: middle;
}

.match-teams {
  display: flex;
  align-items: center;
  justify-content: center;
}

.team-vs {
  display: grid;
  grid-template-columns: minmax(0, 1fr) auto minmax(0, 1fr);
  align-items: center;
  column-gap: 1.25rem;
}

.match-score {
  justify-self: center; /* 그리드 셀에서 가운데 */
  width: 3.75rem;
  height: 1.375rem;
  flex-shrink: 0;
  background: #363636;
  font-size: 0.875rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}

.match-row {
  width: 100%;
  height: 3.75rem;
  border-radius: 0.5rem;
  border: 0.015rem solid #6e6e6e;
  background: rgba(255, 255, 255, 0.4);
  color: #fff;

  font-family: 'Noto Sans', sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.match-list {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.matches-container,
.promotion-matches-container {
  padding-top: 6.25rem;
}

.match-header {
  padding: 0.75rem 0;
  text-align: center;
  align-items: center;
  justify-content: center;
  color: #a5a5a5;
}
.match-round,
.match-location,
.match-date {
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-team {
  display: flex;
  gap: 0.625rem;
  align-items: center;
  justify-content: flex-end;
}

.away-team {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0.625rem;
}

.promotion-matches-container .match-row,
.match-row.minor,
.standings-row.minor {
  background: rgba(110, 110, 110, 0.2);
}
.promotion-matches-container {
  padding-bottom: 6.25rem;
}

.dropdown-group {
  display: flex;
  gap: 1.875rem;
}

.dropdown-container {
  width: 8.125rem;
  height: 3.438rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #c2c2c2;
  font-size: 0.875rem;
  font-weight: 700;
  cursor: pointer;
}

.dropdown-trigger {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0.75rem;
  border-radius: 0.5rem;
  border: 1px solid #e4e7e9;
  background: rgba(110, 110, 110, 0.2);
  color: #c2c2c2;
  font-size: 0.875rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s ease;
}

.dropdown-trigger:hover:not(.disabled) {
  background: rgba(110, 110, 110, 0.35);
}

.dropdown-trigger.open {
  background: rgba(110, 110, 110, 0.35);
}

.dropdown-trigger.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.dropdown-text {
  flex: 1;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.dropdown-arrow {
  width: 1rem;
  height: 1rem;
  color: #c2c2c2;
  transition: transform 0.2s ease;
  flex-shrink: 0;
}

.dropdown-arrow.rotated {
  transform: rotate(180deg);
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 1000;
  margin-top: 0.25rem;
  background-color: #2a2a2a;
  border: 1px solid #444;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.dropdown-list {
  list-style: none;
  margin: 0;
  padding: 0.25rem 0;
}

.dropdown-option {
  width: 100%;
  padding: 0.5rem 0.75rem;
  background: none;
  border: none;
  color: #c2c2c2;
  font-size: 0.875rem;
  font-weight: 700;
  text-align: left;
  cursor: pointer;
  transition: transform-color 0.15s ease;
}

.dropdown-option:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.dropdown-option.selected {
  background-color: #3b82f6;
  color: white;
}

.dropdown-option.selected:hover {
  background-color: #2563eb;
}

.selected-values {
  background-color: #333;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-top: 1.25rem;
}

.selected-values p {
  margin: 0.25rem 0;
}

/* 스크롤바 스타일링 */

.dropdown-menu::-webkit-scrollbar-track {
  background: #1a1a1a;
}

.dropdown-menu::-webkit-scrollbar-thumb {
  background: #666;
  border-radius: 3px;
}

.dropdown-menu::-webkit-scrollbar-thumb:hover {
  background: #888;
}

/* 접근성 개선 */
.dropdown-trigger:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

.dropdown-option:focus {
  background-color: rgba(255, 255, 255, 0.1);
  outline: none;
}

/* 애니메이션 */
.dropdown-menu {
  animation: dropdownFadeIn 0.15s ease-out;
}

@keyframes dropdownFadeIn {
  from {
    opacity: 0;
    transform: translateY(-5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
